<template>
    <div>
        <adt-search-folder
            :searchRouter="{query: {taskNumber: search.taskNumber, taskName: search.taskName, taskClassify: search.taskClassify}}"
            @on-triggerFolder="searchChange" :isStatus="true">
            <template slot="status">
                <div class="status">
                    <Col span="10">
                        <div class="name">审核状态:</div>
                        <span v-for="(status, id) in auditStatusList" :key="id" @click="auditStatus(id)" :class="{'click-status': auditStatusNum == id}">
                            {{status.status}}
                        </span>
                    </Col>
                    <Col span="14">
                        <div class="name">启用状态:</div>
                        <span v-for="(status, id) in enableStatusList" :key="id" @click="enableStatus(id)" :class="{'click-status': enableStatusNum == id}">
                            {{status.status}}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <Col span="6" class="search-condition">
                    <span>异常编号:</span>
                    <Input v-model="search.taskNumber" clearable></Input>
                </Col>
                <Col span="6" class="search-condition line-margin">
                    <span>异常名称:</span>
                    <Input v-model="search.taskName" clearable></Input>
                </Col>
                <Col span="6" class="search-condition line-margin">
                    <span>异常分类:</span>
                    <Select v-model="search.taskClassify" clearable>
                        <Option v-for="(item, index) in taskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </Col>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <AdtButton class="btn-operating">
                <Button @click="add()">
                    新增异常<Icon type="ios-add-circle-outline"></Icon>
                </Button>
            </AdtButton>
            <adt-paginator @getPages="getPages" class="pagination" :currentPage="~~page" :total="~~total" />
        </div>
        <Table :columns="tableColumns" :data="tableData" stripe>
            <!-- 审核状态 -->
            <template slot-scope="{ row, index }" slot="auditStatus">
                <span :class="{success: row.statusCode == 'pass', failure: row.statusCode == 'failed'}">{{row.status}}</span>
            </template>
            <!-- 启用状态 -->
            <template slot-scope="{ row, index }" slot="enableStatus">
            </template>
            <!-- 操作 -->
            <template slot-scope="{ row, index }" slot="action">
                <Icon class="icon-action" type="md-create" @click="edit(row)" />
                <Icon class="icon-action" type="md-close" @click="remove(row)" />
                <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
            </template>
        </Table>
        <!-- 新增、编辑异常 -->
        <adt-my-modal
            title="新增异常"
            :showModal="isModalShow"
            @on-visible-change="addVisibleChange"
            @on-ok="save()"
            @on-cancel="cancel()"
            width="650">
            <Form
                ref="formValidate"
                inline
                :model="unusualData"
                :rules="ruleValidate"
                :label-colon="true"
                label-position="left"
                :label-width="120"
                class="two-column">
                <FormItem label="异常编号" style="width: 100%;">
                    {{ 'GDF534534' }}
                </FormItem>
                <FormItem label="异常名称" prop="taskName">
                    <Input v-model="unusualData.taskName" />
                </FormItem>
                <FormItem label="适用场景" prop="taskClassify">
                    <Select v-model="unusualData.taskClassify" clearable>
                        <Option v-for="(item, index) in taskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
                <FormItem label="异常类型" prop="taskName">
                    <Input v-model="unusualData.taskName" />
                </FormItem>
                <FormItem label="触发逻辑类型" prop="taskClassify">
                    <Select v-model="unusualData.taskClassify" clearable>
                        <Option v-for="(item, index) in taskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
                <FormItem label="触发逻辑描述" prop="taskName">
                    <Input v-model="unusualData.taskName" style="width: 470px;" />
                </FormItem>
                <FormItem label="表达式内容" prop="taskName">
                    <Input type="textarea" v-model="unusualData.taskName" style="width: 470px;" />
                </FormItem>
                <FormItem label="处理机制类型" prop="taskClassify">
                    <Select v-model="unusualData.taskClassify" clearable>
                        <Option v-for="(item, index) in taskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
                <FormItem label="关联场景" prop="taskClassify">
                    <Select v-model="unusualData.cj" clearable multiple>
                        <Option v-for="(item, index) in cj" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
            </Form>
        </adt-my-modal>
        <!-- 审核新增异常 -->
        <adt-my-modal
            title="审核新增异常"
            :showModal="isVerifyAddModalShow"
            @on-visible-change="verifyAddVisibleChange"
            @on-ok="verifyAddSave()"
            @on-cancel="verifyAddCancel()"
            cancelText="不同意"
            okText="同意"
            width="800">
            <div class="public-detail">
                <div class="verify-main-info">
                    <Row>
                        <Col span="14">
                            <div class="name">创建人：</div>
                            <div class="content">裂开了裂开了裂开了裂开了</div>
                        </Col>
                        <Col span="10">
                            <div class="name">创建时间：</div>
                            <div class="content">F545</div>
                        </Col>
                    </Row>
                </div>
                <div class="normal-info">
                    <Row>
                        <Col span="10">
                            <div class="name">异常名称：</div>
                            <div class="content">裂开了裂开了裂开了裂开了</div>
                        </Col>
                        <Col span="10">
                            <div class="name">适用场景：</div>
                            <div class="content">裂开了裂开了裂开了裂开了</div>
                        </Col>
                    </Row>
                    <Row>
                        <Col span="10">
                            <div class="name">异常类型：</div>
                            <div class="content">F545</div>
                        </Col>
                        <Col span="10">
                            <div class="name">触发逻辑类型：</div>
                            <div class="content">F545</div>
                        </Col>
                    </Row>
                    <Row>
                        <Col span="24">
                            <div class="name">触发逻辑描述：</div>
                            <div class="content many-words">裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了裂开了</div>
                        </Col>
                        <Col span="24">
                            <div class="name">表达式内容：</div>
                            <div class="content many-words">裂开了裂开了裂开了裂开了</div>
                        </Col>
                    </Row>
                    <Row>
                        <Col span="24">
                            <div class="name">处理机制类型：</div>
                            <div class="content">裂开了裂开了裂开了裂开了</div>
                        </Col>
                        <Col span="24">
                            <div class="name">关联场景：</div>
                            <div class="content">
                                <ul>
                                    <li>场景1</li>
                                    <li>场景2</li>
                                </ul>
                            </div>
                        </Col>
                    </Row>
                </div>
            </div>
        </adt-my-modal>
        <!-- 审核编辑异常 -->
        <adt-my-modal
            title="审核编辑异常"
            :showModal="isVerifyEditModalShow"
            @on-visible-change="verifyEditVisibleChange"
            @on-ok="verifyEditSave()"
            @on-cancel="verifyEditCancel()"
            cancelText="不同意"
            okText="同意"
            width="800">
            <div class="public-detail">
                <div class="verify-main-info">
                    <Row>
                        <Col span="8">
                            <div class="name">修改人：</div>
                            <div class="content">裂开了裂开了裂开了裂开了</div>
                        </Col>
                        <Col span="8">
                            <div class="name">修改时间：</div>
                            <div class="content">F545</div>
                        </Col>
                        <Col span="8">
                            <div class="name">异常编号：</div>
                            <div class="content">F545</div>
                        </Col>
                    </Row>
                </div>
            </div>
            <div class="public-modify-record">
            <div class="collapse">
                <div class="content">
                    <p class="list">
                        <span class="content-tit">修改异常名称：</span>
                        <ul>
                            <li><i class="ico"></i>运输任务</li>
                        </ul>
                        <span><i class="ico ico-blue"></i>物流运输任务</span>
                    </p>
                    <hr class="line">
                    <p class="list del-item">
                        <span class="content-tit">修改异常名称：</span>
                        <ul>
                            <li>开始时间</li>
                            <li>结束时间</li>
                            <li>运输任务</li>
                        </ul>
                    </p>
                    <hr class="line">
                    <p class="list">
                        <span class="content-tit">新增数据项：</span>
                        <ul>
                            <li>通知企业</li>
                        </ul>
                    </p>
                </div>
            </div>
        </div>
        </adt-my-modal>
        <!-- 提交删除申请 -->
        <adt-reconfirm-modal :showModal="isActionDel" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
import validateRules from '@/libs/validateRules'
export default {
    mixins: [paginator],
    data () {
        return {
            isModalShow: false,
            isVerifyAddModalShow: false,
            isVerifyEditModalShow: false,
            isActionDel: false,
            search: {},
            taskClassifyList: [
                {
                    label: '运输',
                    value: 'ys'
                },
                {
                    label: '场所',
                    value: 'cs'
                }
            ],
            cj: [
                {
                    label: '运输',
                    value: 'ys'
                },
                {
                    label: '场所',
                    value: 'cs'
                }
            ],
            auditStatusNum: '',
            enableStatusNum: '',
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ],
            enableStatusList: [
                {code: 'unreviewed', status: '已启用'},
                {code: 'stock', status: '未启用'},
                {code: 'progress', status: '已禁用'}
            ],
            unusualData: {
                taskName: '',
                taskClassify: ''
            },
            ruleValidate: {
                taskName: [
                    validateRules.required('异常名称')[0]
                ],
                taskClassify: [
                    validateRules.select('异常分类')[0]
                ]
            },
            tableColumns: [
                {
                    title: '异常编号',
                    align: 'center',
                    key: ''
                },
                {
                    title: '异常名称',
                    align: 'center',
                    key: ''
                },
                {
                    title: '异常类型',
                    align: 'center',
                    key: ''
                },
                {
                    title: '异常解决方案',
                    align: 'center',
                    key: ''
                },
                {
                    title: '创建人',
                    align: 'center',
                    key: ''
                },
                {
                    title: '创建时间',
                    align: 'center',
                    key: ''
                },
                {
                    title: '修改人',
                    align: 'center',
                    key: ''
                },
                {
                    title: '修改时间',
                    align: 'center',
                    key: ''
                },
                {
                    title: '审核人',
                    align: 'center',
                    key: ''
                },
                {
                    title: '审核时间',
                    align: 'center',
                    key: ''
                },
                {
                    title: '审核状态',
                    align: 'center',
                    slot: 'auditStatus'
                },
                {
                    title: '启用状态',
                    align: 'center',
                    slot: 'enableStatus'
                },
                {
                    title: '备注',
                    align: 'center',
                    key: ''
                },
                {
                    title: '操作',
                    align: 'center',
                    slot: 'action'
                }
            ],
            tableData: [
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核通过', statusCode: 'pass'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'failed'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'unreviewed'}
            ]
        }
    },
    methods: {
        getPages (page) {
            this.page = page
        },
        addVisibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        verifyAddVisibleChange (changeStatus) {
            this.isVerifyAddModalShow = changeStatus
        },
        verifyEditVisibleChange (changeStatus) {
            this.isVerifyEditModalShow = changeStatus
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        dataInit () {
            this.getData()
        },
        getData () {},
        add () {
            this.isModalShow = true
        },
        searchChange () {},
        // 编辑
        edit (id) {
            this.isModalShow = true
            apiGetTask({ data: { id } }).then(({ data }) => {

            })
        },
        // 删除
        remove () {
            this.isActionDel = true
        },
        allDelClick () {},
        save () {
            this.$refs.formValidate.validate((valid) => {
                if (valid) {
                    const data = { ...this.unusualData }
                    apiAddTask({ data }).then(({ data }) => {

                    })
                }
            })
        },
        cancel () {},
        verifyAddCancel () {},
        detail (row) {
            this.$router.push({path: '/standard-mapper/unusual-manage/detail'})
        },
        auditStatus (index) {
            // 控制状态切换传值
            this.auditStatusNum = index
        },
        enableStatus (index) {
            // 控制状态切换传值
            this.enableStatusNum = index
        }
    },
}
</script>

<style lang="scss" scoped>
h6 {
    padding: 12px 0;
    font-size: $font-size-base;
    font-weight: normal;
    border-top: 2px dashed $border-color;
}
span{
    width: 120px;
    line-height: 30px;
}
p{
    display: inline-block;
    font-size: $font-size-large;
    font-weight: bolder;
    font-family: $base-font-family;
}
.line{
    border:0.5px dashed $border-color;
    margin: 20px 0;
}
// 新增下的列表
.table {
    display: flex;
    p{
        margin: 5px 0;
        font-size: $font-size-base;
    }
    .table-left{
        margin: 0 20px;
    }
}
.search-condition{
    display: flex;
}
.line-margin {
    margin-left: 30px;
}
.icon-action{
    margin: 0 10px;
}
.btn-pagina{
    display: flex;
    padding: 20px 0;

    .btn-operating{
        width: 50%;
    }
    .pagination{
        width: 50%;
        text-align: right;
        margin-right: 2%;
    }
}
</style>
